#markdown-previewer {
    color: rgb(133, 133, 133);
}

#markdown, #previewer {
width: 100%;
max-width: 1000px;
height: auto;
font-size: 4vw;
background-color: #50ffedff;
margin: auto;
padding: 5px;
}

#previewer {
    padding: 0;
}

h5 {
    font-size: 4vw
}

.card-text {
max-width: 100%;
margin-top: 5vh;
}

textarea {
padding: 2vw;
width: 100%;
height: 30vw;
margin-top: 5vh;
}

#previewer {
margin: 5vw auto;
}

pre code {
display: block;
background-color: #000;
color: #fff;
padding: 5%;
}

code {
background-color: #000;
color: #fff;
padding: 1%
}

#preview {
width: 100%;

    img {
    max-width: 100%;
    background-color: #666;
    padding: 5%;
    margin: auto;
    }
}

blockquote {
background: #444;
border-left: 20px solid #666;
margin: 1.5em 10px;
padding: 0.5em 10px;
quotes: "\201C""\201D""\2018""\2019";
}
blockquote:before {
color: #ccc;
content: open-quote;
font-size: 4em;
line-height: 0.1em;
margin-right: 0.25em;
vertical-align: -0.4em;
}
blockquote p {
display: inline;
}

#html-format {
    height: 0;
    overflow: hidden;
}

#html-format.show{
    height: auto;
}

#preview-button {
    border: 5px solid #000;
    margin-bottom: 3vw
}

