.cards {
  margin-top: 5vw;
  min-height: 150vh;
  padding-top: 15vh;
  text-align: center;
}

.cards h1 {
  width: 100vw;
  background-color: #000;
  font-size: 5vw;
  padding: 2vw 0;
  box-shadow: 0px 10px 0 10px rgb(57, 0, 243);
}

.cards__items {
  width: 80%;
  margin: 10vw auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 4vw;
}